import React from 'react'

const TimelineItem = (props) => {
    return(
        <div className="item">
            <div className={"container " + (props.data.highlight ? "highlighted" : "")}>
                <div className={"content " + (props.data.active ? "active" : "")} onClick={() => props.handler(props.index)}>
                    <p className="title">{props.data.title}</p>
                    <p className="date">{props.data.date}</p>
                </div>
            </div>
        </div>
    )
}

export default TimelineItem;