import React, { useState } from 'react';
import './App.css';
import Timeline from './components/Timeline'
import Info from './components/Info'
import timelineData from './data'

import antiAsian from './images/anti-asian-riots.jpeg'
import startOfWar from './images/start-of-ww2.jpeg'
import training from './images/training.png'
import card from './images/card.jpeg'
import pearlHarbor from './images/pearl-harbor.jpeg'
import hongKong from './images/hong-kong.jpeg'
import married from './images/married.png'
import midway from './images/battle-of-midway.jpeg'
import children from './images/children.png'
import bomb from './images/bomb.jpeg'
import endOfWar from './images/end-of-war.jpeg'

function App() {
  const [d, setD] = useState(timelineData)
  const [num, setNum] = useState(0)
  const [animate, setAnimate] = useState(false)
  const [image, setImage] = useState(<img src={antiAsian} alt="anti-asian-riots-jpeg"/>)

  function handler (index){
        if(index < 0){
          index = d.length-1
        }

        if(index >= d.length){
          index=0
        }

        if(animate){
          return;
        }

        let tempD = [...d];
        for(let i =0; i<tempD.length; i++){
                tempD[i].active = false
                tempD[i].highlight = false
        }

        tempD[index].active = true;

        for(let i = 0; i<index; i++){
            tempD[i].highlight = true
        }

        if(tempD[index].img === "anti-asian-riots.jpeg"){
          setImage(<img src={antiAsian} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "start-of-ww2.jpeg"){
          setImage(<img src={startOfWar} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "training.png"){
          setImage(<img src={training} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "card.jpeg"){
          setImage(<img src={card} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "pearl-harbor.jpeg"){
          setImage(<img src={pearlHarbor} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "hong-kong.jpeg"){
          setImage(<img src={hongKong} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "married.png"){
          setImage(<img src={married} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "battle-of-midway.jpeg"){
          setImage(<img src={midway} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "children.png"){
          setImage(<img src={children} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "bomb.jpeg"){
          setImage(<img src={bomb} alt={tempD[index].img}/>)
        }else if(tempD[index].img === "end-of-war.jpeg"){
          setImage(<img src={endOfWar} alt={tempD[index].img}/>)
        }else{
          setImage(<></>)
        }

        setD(tempD)
        setNum(index)
        changeTheme(index)
        setAnimate(true)
  }

  function changeTheme (index){
    if(d[index].type === 0){
      document.documentElement.style.setProperty('--primary','tomato')
    }
    if(d[index].type === 1){
      document.documentElement.style.setProperty('--primary','#ab20fd')
    }
    if(d[index].type === 2){
      document.documentElement.style.setProperty('--primary','#00af41')
    }
  }

  function handleAnimate(){
    setAnimate(false)
  }

  document.onkeydown = (e) => {
    e.preventDefault()
    if(e.key === "ArrowLeft"){
      handler(num-1)
    }
    if(e.key === "ArrowRight"){
      handler(num+1)
    }
  }

  return (
    <div className="App">
      <h1>Timeline 1907 - 1948</h1>
      <Timeline data={d} handler={handler}/>
      <Info data={d} num={num} handler={handler} image={image} animate={animate} handleAnimate={handleAnimate}/>
      <div className="shadow left"></div>
      <div className="shadow right"></div>
    </div>

  );
}

export default App;
