import React from 'react'

const Info = (props) => {
    return(
        <div className="info ">
            <div onAnimationEnd={props.handleAnimate} className={`container ${props.animate ? "animate" : ""}`}>
                <h1>{props.data[props.num].title}</h1>
                <p>{props.data[props.num].date}</p>
                <div className="content">
                    <p>{props.data[props.num].content}</p>
                    {props.image}
                </div>
            </div>
            <svg onClick={()=>props.handler(props.num+1)} className="right" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M12 4l-1.41 1.41L16.17 11H4v2h12.17l-5.58 5.59L12 20l8-8-8-8z"/></svg>
            <svg onClick={()=>props.handler(props.num-1)} className="left" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z"/></svg>
        </div>
    )
}

export default Info